	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>ðŸŒ¡ 26 Â° C â˜€</strong></p>\n<p>Unusually I got up early so I made buildings and vehicles with LEGO block.</p>\n<ul>\n<li>Desk lamp</li>\n<li>A sign</li>\n<li>bus</li>\n<li>Tow truck</li>\n<li>House</li>\n<li>airplane</li>\n<li>airship</li>\n</ul>\n<p>I forgot to stop I watched the recording of the alarm clock ringing and forgot to see yesterday \"Hang on! Tabu-kun !!\".</p>\n<p>Then, when we had a little late breakfast, S came to visit, so we went for lunch together with Famicom, and the father watching TV of F1 race of my father's hobby began.</p>\n<p>After a while TV was going to be occupied, so I went to play <a href=\"../09/24-diary.md\">the park</a> we <a href=\"../09/24-diary.md\">mapped last week</a> with S.</p>\n<p>When I got home, I removed the remaining homework and still had time to spare, so I watched \"Doraemon Nobita and Animal Planet\".</p>","frontmatter":{"cover":null,"date":"1990-10-02T23:30:00+09:00","strDate":"1990/10/2","title":"diary"}}},"pathContext":{"path":"/en/blog/1990/10/01-diary/","langKey":"en"}}

